import React from 'react'
import "../css/navbar.css"
import { NavLink } from 'react-router-dom'

const Navbar = () => {
  return (
    <div id="navbar-maindiv">
      <nav id="navbar">
      <ul className="nav-list">
          <li className="nav-item"><NavLink to="/home" className='nav-link'>Home</NavLink></li>
          <li className="nav-item"><NavLink to="/login" className='nav-link'>Login</NavLink></li>
          <li className="nav-item"><NavLink to="/signup" className='nav-link'>Signup</NavLink></li>
          <li className="nav-item"><NavLink to="/calculater" className='nav-link'>Calculater</NavLink></li>
          <li className='nav-item'><NavLink to="/employeelist" className='nav-link'>Employee List</NavLink></li>
          <li className='nav-item'><NavLink to="/pagination" className='nav-link'>Pagination</NavLink></li>
      </ul>
    </nav>
    </div>
  )
}

export default Navbar