import React from 'react'
import '../css/calculator.css'

const Calculater = () => {

  const [value, setValue] = React.useState('');

  return (
    <div id="main-container-calculator">

      <div id="calc-mainbox">


          {/* Result Box */}
        <div id="result-box">

            <div id="equation-div">
              <p>{value}</p>
            </div>

            <div id="result-div">
              {/* <p>{value}</p> */}
            </div>

        </div>


          {/* Number Box */}
         <div id="number-box">

          <div>
            <p onClick={() => setValue(value + '')}>AC</p>
          </div>
          <div>
            <p onClick={() => setValue()}>Del</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '()')}>()</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '/')}>/</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '7')}>7</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '8')}>8</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '9')}>9</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '*')}>x</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '4')}>4</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '5')}>5</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '6')}>6</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '-')}>-</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '1')}>1</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '2')}>2</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '3')}>3</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '+')}>+</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '.')}>.</p>
          </div>
          <div>
            <p onClick={() => setValue(value + '0')}>0</p>
          </div>
          <div id="equalto-div" className='equalto-div'>
            <p onClick={() => setValue(eval(value))}>=</p>
          </div>

         </div>

      </div> 
      

    </div>
  )
}

export default Calculater