import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import "../css/home.css";
import images1 from "../images/undraw.png";
import { Link } from "react-router-dom";

const Home = () => {
  return (
    <div id="maincontainer">
      <div id="child-homecontainer">
        {/* Icons */}
        <div id="icons-div">
          <div className="icon">
            {" "}
            <a
              href="https://www.linkedin.com/in/loganathan-g-030334242"
              target="_blank"
              rel="noopener noreferrer"
            >
              <FontAwesomeIcon icon={faLinkedin} />
            </a>
          </div>

          <div className="icon">
            <a
              href="https://github.com/G-Loganathan"
              target="_blank"
              rel="noopener noreferrer"
            >
              <FontAwesomeIcon icon={faGithub} />
            </a>
          </div>
        </div>

        {/* Content */}
        <div id="content-div">
          <div className="name-div">
            <p>Hi, I'm Loganathan.</p>
          </div>

          <div className="jobtitle-div">
            <p>Full Stack Developer</p>
          </div>

          <div className="para-div">
            <p>
              A MERN Stack Developer building the Frontend and Backend of
              Websites and Web Applications that leads to the success of the
              overall product.
            </p>
          </div>

          <div className="btn-div">
            {/* <button>Download CV</button> */}
            <a href="/Loganathan.pdf">DOWNLOAD CV</a>
          </div>
        </div>

        {/* Image */}

        <div id="image-div">
          <img src={images1} alt="" />
        </div>
      </div>

      {/* About */}

      <div id="aboutcontainer">
        <div id="aboutme-box">
          <div id="about-title">
            <p>About Me</p>
          </div>

          <div id="small-line-div">
            <hr className="small-line" />
          </div>

          <div id="about-subline">
            <p>
              Here you will find more information about me, what I do, and my
              current skills mostly in terms of programming and technology
            </p>
          </div>

          {/* MAin */}

          <div id="main-container">
            {/* About Box Starts */}
            <div id="aboutme-content-box">
              <div id="getme-title-div">
                <p>Get to know Me!</p>
              </div>

              <p>
                I'm a Fullstack Developer building the Front-end and Back-end of
                Websites and Web Applications that leads to the success of the
                overall product.
              </p>
              <p>
                I also like sharing content related to the stuff that I have
                learned over the years in Web Development so it can help other
                people of the Dev Community. Feel free to Connect or Follow me
                on my Linkedin where I post useful content related to Web
                Development and Programming.
              </p>
              <p>
                I'm open to Job opportunities where I can contribute, learn and
                grow. If you have a good opportunity that matches my skills and
                experience then don't hesitate to contact me.
              </p>

              {/* <div id="download-cv-div">
                      <button>Download CV</button>
                    </div> */}
            </div>

            {/* About Box Ends */}

            {/* Skill Box Starts */}
            <div id="aboutme-right-container">
              <div id="myskills-box">
                <div id="myskills-title-div">
                  <p>My Skills</p>
                </div>

                <div id="myskills-list">
                  <div className="myskills-item">HTML</div>
                  <div className="myskills-item">CSS</div>
                  <div className="myskills-item">JavaScript</div>
                  <div className="myskills-item">React</div>
                  <div className="myskills-item">Git</div>
                  <div className="myskills-item">GitHub</div>
                  <div className="myskills-item">C</div>
                  <div className="myskills-item">MySQL</div>
                </div>
              </div>

              <div id="contactinfo-div">
                <h1>Contact Me</h1>
                <p>
                  Name: <span>Loganathan G</span>
                </p>
                <p>
                  Email: <span>loganathan2624@gmail.com</span>
                </p>
                <p>
                  Phone: <span>7904497010</span>
                </p>
              </div>
            </div>

            {/* Skill Box Ends */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;
