import React from "react";
import "../css/login.css";
import {Link} from 'react-router-dom';
import {useForm} from "react-hook-form";
import Swal from 'sweetalert2';

const Login = () => {

    const { register, handleSubmit, formState:{errors} } = useForm();

    const onSubmit = (data) => {
        console.log(data);
        Swal.fire({
            toast: true,
            position: "top-end",
            icon: "success",
            title: "Data saved successfully!",
            showConfirmButton: false,
            timer: 1500
        });
    }


    return (
        <div>
            <div id="logincontainer">
                <form onSubmit={handleSubmit(onSubmit)}>

                    {/* Username */}
                    <div className="input-group">
                        <label htmlFor="username">Username</label>
                        <input type="text" id="username" name="username" {...register('username',{
                            required:{
                                value:true,
                                message:"Username is required"
                            },
                            minLength:{
                                value:3,
                                message:"Username should have 3 characteres"
                            },
                            maxLength:{
                                value:10,
                                message:"Username should be maximum 10 characters"
                            }
                        })}/>
                        <div>
                            <p className="login-error-msg">{errors.username?.message}</p>
                        </div>
                        <span className="error"></span>
                    </div>

                    {/* Password */}
                    <div className="input-group">
                        <label htmlFor="password">Password</label>
                        <input type="password" id="password" name="password" {...register('password',{
                            required:{
                                value:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,10}$/,
                                message:"Password must be 8 to 10 characters, at least one uppercase and lowercase letters, number and special character:"
                            }
                        })}/>
                        <div>
                            <p className="login-error-msg">{errors.password?.message}</p>
                        </div>
                    </div>

                    {/* Button */}
                    <div id="button-div">
                        <button type="submit">Log in</button>
                    </div>

                    {/* Sign up link */ }
                    <div id="signup-link-div">
                        <p>Don't have an account?<Link to="/signup">Sign up</Link></p>
                    </div> 
                </form>
            </div>
        </div>
    )
}

export default Login;

